import React from 'react';
import * as rax from 'retry-axios';
import axios from 'axios';


class Output extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            people:[],
            pagination:{page:0, length:0},
            profileRequestStatus:false
        };
    }

    async componentDidMount(){
        const interceptorId = rax.attach();
        console.log("Making an API call.");
        var resp = await axios({
            "method":"get",
            "url":"https://randomuser.me/api/?results=200",
            "headers": { "Content-Type": "application/x-www-form-urlencoded"},
            raxConfig: {
                retry: 6,
                noResponseRetries: 5,
                retryDelay: 500,
                onRetryAttempt: err => {
                    console.log("Network error, we will try to reconnect.");
                }
            }
        }).then((resp) => {
            console.log("API Call succeeded.")
            return resp;
        });

        this.setState({
            pagination:{
                length:resp.data.results.length/20,
                page: this.state.pagination.page = 1
            },
            people: resp.data.results.map((item)=>{
                var obj = {};
                obj.name = item.name.first + " " + item.name.last;
                obj.picture = item.picture.large;
                obj.age = item.dob.age;
                obj.gender = item.gender;
                obj.email = item.email;
                return obj;
            })
        });
    }

    renderComponents = (people, from, to) => {
        var results = people.map((person, index) => {
                return (<div key={index} className="profile-container">
                    <div className="profile">

                        <div style={{backgroundImage:'url('+person.picture+')'}} className="profile-picture">
                        </div>
                        <div className="profile-content">
                            <div className="profile-content-title">
                                {person.name}
                            </div>
                            <ul className="profile-content-info">
                                <li>
                                    <span>Age:</span>
                                    <span>{person.age}</span>
                                </li>
                                <li>
                                    <span>Gender:</span>
                                    <span>{person.gender}</span>
                                </li>
                                <li>
                                    <span>Email:</span>
                                    <span>{person.email}</span>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>)
            });
        this.state.pagination.length = Math.ceil(results.length / 20);
        if(this.state.pagination.page > this.state.pagination.length || (this.state.pagination.page == 0 && this.state.pagination.length != 0)) {
            this.state.pagination.page = 1;
        }
        return results.slice(this.state.pagination.page * 20 - 20, this.state.pagination.page * 20);
    };

    filterByForm = (form) => {
        var query = {name:form.name.toLowerCase(), age:form.age, gender: form.gender.toLowerCase()};
        var people = this.state.people.filter(function (item){
            let namePass = true;
            let agePass = true;
            let genderPass = true;
            if(query.name !== "") {
                namePass = item.name.toLowerCase().lastIndexOf(query.name) !== -1
            }

            if(query.age.h != "") {
                if(item.age >= query.age.l && item.age < query.age.h) {
                    agePass = true;
                } else {
                    agePass = false;
                }
            }

            if(query.gender != "") {
                genderPass = item.gender.toLowerCase() == query.gender;
            }
            return (namePass && agePass && genderPass);
        });
        return people;
    };

    changePagination = (e) => {
        var s = parseInt(e.currentTarget.id.replace(/[^0-9]+/,''));
        this.setState({pagination:{page:s, length:this.state.pagination.length}})
    };

    decrementPagination = (e) => {
        this.setState({pagination:{page: this.state.pagination.page - 1, length:this.state.pagination.length}})
    };

    incrementPagination = (e) => {
        this.setState({pagination:{page:this.state.pagination.page + 1, length:this.state.pagination.length}})
    };

    loopAr = (times) => {
        var arr = [];
        for ( var i = 1; i <= times; i++) {
            arr.push(i);
        }
        return arr;
    }

    render () {
        const people = this.renderComponents(
                this.filterByForm(this.props.form)
            );
// [1,2,3,4,5,6,7,8]

        const pagination = this.loopAr(this.state.pagination.length).filter((i) => {
            if(this.state.pagination.page > i && i == this.state.pagination.page  -1) {
                return true;
            } else if (this.state.pagination.page  == i) {
                return true;
            } else if (this.state.pagination.page  < i && this.state.pagination.page  > i - 3 && this.state.pagination.length  >= i) {
                return true;
            } else if (this.state.pagination.page >= this.state.pagination.length - 1 && i + 3 > this.state.pagination.page) {
                return true;
            }
        })
        .map((item) => {
            return (<div onClick={this.changePagination}
                id={"pagination-id-"+item}
                key={item}
                className={"pagination-entry " + ((item == this.state.pagination.page) ? "active" : "")}>
            <span>{item}</span></div>)
        });

        var  left = (this.state.pagination.page > 1) ? true : false
        if (left) {
            var leftEl = (<div onClick={this.decrementPagination} id="p1"className="pagination-handle">Previous</div>)
        }
        var right = (this.state.pagination.page < this.state.pagination.length) ? true : false
        if (right) {
            var rightEl = (<div onClick={this.incrementPagination} id="p2" className="pagination-handle">Next</div>)
        }

        return (
         <div className="main-container">
            <h3 className="main-title"> {people.length} results</h3>
            {
                people
            }
            <div className="main-pagination">
                {
                    leftEl
                }
                {
                    pagination
                }
                {
                    rightEl
                }

            </div>
        </div>
        );

    }
}
export default Output;
