import React from 'react';


class Sidebar extends React.Component {


    constructor(props) {
        super(props);
        this.state = {
            profileShow: false
        };
    }

    showMenu = () => {
        let s = (this.state.profileShow == true) ? false : true;
        this.setState({profileShow:s});
    }

    searchNameChange = (e) => {
        this.props.form.name = e.currentTarget.value;
        this.props.callback(this.props.form);
    }

    searchAgeChange = (e) => {
        let v = parseInt(e.currentTarget.value);
        this.props.form.age = {l:v,h:v+10};
        this.props.callback(this.props.form);
    }

    searchGenderChange = (e) => {
        this.props.form.gender = e.currentTarget.value;
        this.props.callback(this.props.form);
    }


    render(){
      return (
        <div className="sidebar">
          <button onClick={this.showMenu} className={"menu-button " + (this.state.profileShow ?"active" : "")}>Refine <img src="funnel.png" alt="" /></button>
          <div className={"sidebar-main " + (this.state.profileShow ? 'show' : "")}>
              <div className="title">
                  <h5>Refine</h5>
              </div>
              <div id="searchForm" className="search-form">
                  <div className="search-input">
                      <input onKeyUp={this.searchNameChange} id="search-name" type="text" name="name" placeholder="Willie"/>
                      <label htmlFor="name">Name</label>
                  </div>
                  <div className="search-input">
                      <select defaultValue="" onChange={this.searchAgeChange} id="search-age" name="age" id="age">
                          <option value="0">0-10</option>
                          <option value="10">10-20</option>
                          <option value="20">20-30</option>
                          <option value="30">30-40</option>
                          <option value="40">40-50</option>
                        </select>
                        <label htmlFor="age">Age</label>
                  </div>
                  <div className="search-input">
                      <label htmlFor="">Gender</label>
                      <div className="form-group">
                          <input onChange={this.searchGenderChange} type="radio" id="male" name="gender" value="male" />
                          <label htmlFor="male">Male</label>
                      </div>
                      <div className="form-group">
                          <input onChange={this.searchGenderChange} type="radio" id="female" name="gender" value="female" />
                          <label htmlFor="female">Female</label>
                      </div>
                  </div>
              </div>
          </div>
        </div>
      );
    }
}

export default Sidebar;
